/**@jsx jsx */
import { jsx } from 'theme-ui';
import React, { useEffect, useRef } from 'react';
import styled from '@emotion/styled';
import { Link } from 'gatsby';

const StyledSlider = styled.span`
  position: absolute;
  bottom: 0;
  border-width: 1px 0;
  border-style: solid;
  height: 0;
  content: '';
  width: 5rem;
  transition: left 300ms cubic-bezier(0.1, -0.6, 0.2, 0);
  opacity: 0;
`;

const StyledDesktopMenu = styled.ul`
  position: relative;
  display: flex;
  justify-content: flex-start;
  margin-right: auto;
  height: 100%;
`;

const NavItem = styled.li`
  position: relative;
  display: flex;
  flex-grow: 0;
  flex-shrink: 0;
  height: 100%;
  align-items: stretch;
  text-align: center;
`;

const StyledLink = styled(Link)`
  display: flex;
  align-items: center;
  font-weight: 500;
  letter-spacing: 0.15em;
  position: relative;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 0.15em;

  padding: 1rem;
  line-height: 1.5;
`;

const SlideMenu = (props) => {
  const { activeTag } = props;
  const sliderBar = useRef(null);
  const slider = useRef(null);
  const activeItem = useRef(null);
  useEffect(() => {
    let id = null;
    if (sliderBar.current && slider.current && activeItem.current) {
      id = setTimeout(() => {
        let { x: sliderBarX } = sliderBar.current.getBoundingClientRect();
        let { width: sliderWidth } = slider.current.getBoundingClientRect();
        let { x, width } = activeItem.current.getBoundingClientRect();
        let offset = x - sliderBarX + width / 2 - sliderWidth / 2;
        slider.current.setAttribute('style', `left: ${offset.toPrecision(3)}px;opacity: 1;`);
      }, 0);
    }
    return () => {
      if (id) {
        clearTimeout(id);
      }
    };
  }, []);

  const items = [
    {
      url: '/',
      name: 'home',
    },
    {
      url: '/posts',
      name: 'posts',
    },
    {
      url: '/categories',
      name: 'categories',
    },
    {
      url: '/tags',
      name: 'tags',
    },
    {
      url: '/about',
      name: 'about',
    },
  ];

  return (
    <>
      <StyledDesktopMenu
        onMouseOver={(e) => {
          let target = (e || window.event).target;
          if (target == slider.current) {
            return;
          }
          let { x: sliderBarX } = sliderBar.current.getBoundingClientRect();
          let { width: sliderWidth } = slider.current.getBoundingClientRect();
          let { x, width } = target.getBoundingClientRect();
          let offset = x - sliderBarX + width / 2 - sliderWidth / 2;
          slider.current.setAttribute('style', `left: ${offset.toPrecision(3)}px;opacity: 1;`);
        }}
        onMouseLeave={(e) => {
          let target = (e || window.event).target;
          if (target == slider.current) {
            return;
          }
          let { x: sliderBarX } = sliderBar.current.getBoundingClientRect();
          let { width: sliderWidth } = slider.current.getBoundingClientRect();
          let { x, width } = activeItem.current.getBoundingClientRect();
          let offset = x - sliderBarX + width / 2 - sliderWidth / 2;
          if (offset >= 0) {
            slider.current.setAttribute('style', `left: ${offset}px;opacity: 1;`);
          } else {
            slider.current.setAttribute('style', `left: ${offset}px;opacity: 0;`);
          }
        }}
        ref={sliderBar}
      >
        {items.map(({ url, name }) => {
          if (name != activeTag) {
            return (
              <NavItem key={name}>
                <StyledLink
                  to={url}
                  sx={{
                    fontSize: 1,
                  }}
                >
                  {name}
                </StyledLink>
              </NavItem>
            );
          } else {
            return (
              <NavItem key={name} ref={activeItem}>
                <StyledLink
                  to={url}
                  sx={{
                    fontSize: 1,
                  }}
                >
                  {name}
                </StyledLink>
              </NavItem>
            );
          }
        })}
      </StyledDesktopMenu>
      <StyledSlider ref={slider} sx={{ borderColor: 'primary' }}></StyledSlider>
    </>
  );
};

export default SlideMenu;
