import React from 'react';
import PropTypes from 'prop-types';
import Helmet from 'react-helmet';
import { useStaticQuery, graphql } from 'gatsby';

const SEO = ({ title, description, lang, keywords, isBlogPost }) => {
  const { site } = useStaticQuery(graphql`
    query SEO {
      site {
        siteMetadata {
          title
          description
          url
        }
      }
    }
  `);
  const siteDescription = description || site.siteMetadata.description;
  const webTitle = title || site.siteMetadata.title;
  return (
    <Helmet htmlAttributes={{ lang }} titleTemplate={`%s | ${site.siteMetadata.title}`}>
      {/* General tags */}
      <title>{webTitle}</title>
      <meta name="description" content={siteDescription} />
      <link rel="canonical" href={site.siteMetadata.url} />

      {/* OpenGraph tags */}
      <meta property="og:url" content={site.siteMetadata.url} />
      {isBlogPost ? <meta property="og:type" content="article" /> : null}
      <meta property="og:title" content={title} />
      <meta property="og:description" content={site.siteMetadata.description} />
      {keywords.length > 0 && <meta name="keywords" content={keywords.join(', ')} />}
    </Helmet>
  );
};

SEO.defaultProps = {
  lang: `en`,
  meta: [],
  keywords: [],
};

SEO.propTypes = {
  lang: PropTypes.string,
  meta: PropTypes.arrayOf(PropTypes.string),
  keywords: PropTypes.arrayOf(PropTypes.string),
};

export default SEO;
