/**@jsx jsx */
import { jsx } from 'theme-ui';
import React, { useState, useEffect } from 'react';
import { StaticQuery, graphql, Link } from 'gatsby';
import styled from '@emotion/styled';
import Hamburger from './Hamburger/Hamburger';
import MobileMenu from './Menu/MobileMenu';
import SlideMenu from './Menu/SlideMenu';
import Modal from './Modal/Modal';
import ColorButton from './ColorModeButton';
import Signature from '../SVG/Signature';

import usePageScroll from '../../hooks/usePageScroll';
import useWindowResize from '../../hooks/useWindowResize';

const StyledHeader = styled.header`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 50px;
  transition: all 200ms ease-in;
  z-index: 2;
`;

const StyledContainer = styled.div`
  position: relative;
  margin: 0 auto;
  display: flex;
  flex-flow: row nowrap;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  height: 100%;
  z-index: 2;
`;

const StyledIconWrapper = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 1rem;
`;

const StyledDesktopMenuWrapper = styled.div`
  flex-grow: 1;
  flex-shrink: 0;
  display: flex;
  justify-content: space-between;
  margin-left: 2rem;
`;

const StyledNavStart = styled.div`
  position: relative;
  display: flex;
  height: 100%;
`;

const StyledNavEnd = styled.div`
  display: flex;
  align-items: center;
  height: 100%;
`;

const Header = ({ activeTag }) => {
  const { width } = useWindowResize();
  const { lastScroll, isDown } = usePageScroll();
  const [isOpen, toggle] = useState(false);
  const [isMobile, setMobile] = useState(true);
  // responsive
  useEffect(() => {
    if (width > 960) {
      // desktop
      setMobile(false);
      // close menu
      toggle(false);
    } else if (!isMobile) {
      setMobile(true);
    }
  }, [width]);
  return (
    <StyledHeader
      style={{
        transform: !isMobile && isDown ? 'translateY(-100%)' : null,
        boxShadow:
          (lastScroll > 0 && !isDown) || isMobile ? '0px 2px 3px rgba(0, 0, 0, 0.25)' : null,
      }}
      sx={{
        backgroundColor: 'background',
      }}
    >
      <StyledContainer>
        <StyledIconWrapper>
          <Link to="/">
            <img src="/svgs/orbit.svg" height="30" width="30" alt="logo" />
          </Link>
          {!isMobile && (
            <Link
              to="/"
              sx={{
                px: 2,
                fontSize: 2,
                fontWeight: 'bold',
                fontFamily: 'heading',
                letterSpacing: '0.15rem',
              }}
            >
              <Signature />
            </Link>
          )}
        </StyledIconWrapper>
        <StyledDesktopMenuWrapper style={{ display: !isMobile ? 'flex' : 'none' }}>
          <StyledNavStart>
            <SlideMenu activeTag={activeTag}></SlideMenu>
          </StyledNavStart>
          <StyledNavEnd>
            <ColorButton />
          </StyledNavEnd>
        </StyledDesktopMenuWrapper>
      </StyledContainer>

      {isMobile && (
        <Hamburger toggleBurger={(isOpen) => toggle(isOpen)} isOpen={isOpen}></Hamburger>
      )}
      {isOpen && isMobile && <Modal closeModal={() => toggle(false)} />}
      {isMobile && <MobileMenu isOpen={isOpen}></MobileMenu>}
    </StyledHeader>
  );
};

export default ({ activeTag }) => (
  <StaticQuery
    query={graphql`
      query HeaderQuery {
        site {
          siteMetadata {
            title
          }
        }
      }
    `}
    render={(data) => <Header data={data} activeTag={activeTag} />}
  />
);
