/**@jsx jsx */
import { jsx } from 'theme-ui';
import React from 'react';
import styled from '@emotion/styled';

const StyledSpan = styled.span`
  transform-origin: 1px;
  position: relative;
  width: 2rem;
  border-radius: 8px;
  height: 0.25rem;
  transition: all 100ms ease-out;
  &:nth-child(1) {
    transform: ${({ isOpen }) => (isOpen ? 'rotate(45deg)' : 'rotate(0deg)')};
  }

  &:nth-child(2) {
    opacity: ${({ isOpen }) => (isOpen ? '0' : '1')};
  }

  &:nth-child(3) {
    transform: ${({ isOpen }) => (isOpen ? 'rotate(-45deg)' : 'rotate(0deg)')};
  }
`;

const StyledBurger = styled.div`
  position: absolute;
  z-index: 2;
  border: none;
  background: transparent;
  height: 2rem;
  width: 2rem;
  top: 0.5rem;
  right: 1rem;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-around;
  cursor: pointer;
`;

const Hamburger = ({ toggleBurger, isOpen, ...props }) => {
  return (
    <StyledBurger aria-expanded={isOpen} onClick={() => toggleBurger(!isOpen)} {...props}>
      <StyledSpan
        isOpen={isOpen}
        sx={{
          backgroundColor: 'offblack',
        }}
      />
      <StyledSpan
        isOpen={isOpen}
        sx={{
          backgroundColor: 'offblack',
        }}
      />
      <StyledSpan
        isOpen={isOpen}
        sx={{
          backgroundColor: 'offblack',
        }}
      />
    </StyledBurger>
  );
};

export default Hamburger;
