import { useState, useEffect } from 'react';
import ResizeObserver from 'resize-observer-polyfill';
import debounce from 'lodash/debounce';

// Hook for measuring using ResizeObserver Web API
// https://developer.mozilla.org/en-US/docs/Web/API/ResizeObserver

const _document = typeof document !== 'undefined' ? document : {};
/**
 * ref for setting up reference
 * bounds for contentRect which is DOMRectReadOnly
 * @param {Array}
 */
function useWindowResize() {
  const [bounds, set] = useState({ left: 0, top: 0, width: 0, height: 0 });
  // use a debounced function with resizeObserver althought it runs 60fps in browser.
  const debouncedSet = debounce(([entry]) => set(entry.contentRect), 250, {
    trailing: true,
    leading: false,
  });
  const [ro] = useState(() => new ResizeObserver(debouncedSet));
  useEffect(() => {
    ro.observe(_document.documentElement);
    return () => {
      ro.disconnect();
    };
  }, []);
  return bounds;
}

export default useWindowResize;
