/**@jsx jsx */
import { jsx } from 'theme-ui';
import React from 'react';
import { Link } from 'gatsby';
import styled from '@emotion/styled';

const NavItem = styled.li`
  position: relative;
  display: flex;
  flex-grow: 0;
  flex-shrink: 0;
  height: 70px;
  align-items: stretch;
  width: 100%;
`;

const StyledLink = styled(Link)`
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;

  font-size: 100%;
  font-weight: 500;
  letter-spacing: 0.15em;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  letter-spacing: 0.15em;

  padding: 1rem;
  line-height: 1.5;
`;

const StyledMobileMenu = styled.div`
  z-index: 1;
  > ul {
    position: absolute;
    width: 100%;
    display: flex;
    flex-flow: column nowrap;
    transition: transform 300ms ease-out;
    transform: ${({ isOpen }) => (isOpen ? 'translateY(0px)' : 'translateY(-150%)')};
  }
`;

const MobileMenu = ({ isOpen }) => {
  const items = [
    {
      url: '/',
      name: 'home',
    },
    {
      url: '/posts',
      name: 'posts',
    },
    {
      url: '/categories',
      name: 'category',
    },
    {
      url: '/tags',
      name: 'tags',
    },
    {
      url: '/about',
      name: 'about',
    },
  ];
  return (
    <StyledMobileMenu isOpen={isOpen}>
      <ul
        sx={{
          backgroundColor: 'background',
        }}
      >
        {items.map(({ url, name }) => {
          return (
            <NavItem key={name}>
              <StyledLink
                to={url}
                target="_self"
                sx={{
                  color: 'black',
                  '&:hover': {
                    color: 'primary',
                  },
                }}
              >
                {name}
              </StyledLink>
            </NavItem>
          );
        })}
      </ul>
    </StyledMobileMenu>
  );
};

export default MobileMenu;
