/**@jsx jsx */
import { jsx, useColorMode } from 'theme-ui';
import React from 'react';
import styled from '@emotion/styled';
import { Sun, Moon } from 'react-feather';
const modes = ['default', 'dark'];

const getModeName = (mode) => {
  switch (mode) {
    case 'dark':
      return (
        <Moon
          sx={{
            fill: 'gray',
            color: 'gray',
          }}
        />
      );
    case 'default':
      return <Sun sx={{ color: 'orange', fill: 'orange' }} />;
    default:
      return mode;
  }
};
const Button = styled.button`
  background: transparent;
  appearance: none;
  font-family: 'inherit';
  cursor: pointer;
`;

export default (props) => {
  const [mode, setMode] = useColorMode();
  const cycleMode = (e) => {
    const i = modes.indexOf(mode);
    const next = modes[(i + 1) % modes.length];
    setMode(next);
  };
  return (
    <Button
      {...props}
      sx={{
        fontSize: 1,
        fontWeight: 'bold',
        m: 0,
        px: 2,
        py: 2,
        color: 'text',
        border: 0,
        borderRadius: 2,
        '&:hover': {
          color: 'primary',
        },
      }}
      onClick={cycleMode}
    >
      {getModeName(mode)}
    </Button>
  );
};
